    </div>
    <ModalCommon ref="refModal" :navigate="false">
      <swiper
        :loop="true"
        :hashNavigation="{
      watchState: true,
    }"
        :pagination="{
      clickable: true,
    }"
        :spaceBetween="10"
        :navigation="true"
        :initialSlide="showImg"
        :thumbs="{ multipleActiveThumbs: false, swiper: thumbsSwiper }"
        :modules="modules"
        class="mySwiper2"
      >
        <swiper-slide
        v-for="(item, i) in dataDetail.imageLinks" :key="i"
          ><img :src="require(`@/assets/image/${item}`)"
