<script setup>
import BoxCommon from "@/components/common/box-common.vue";
import ModalCommon from "@/components/common/modal-common.vue";
import vScrollClass from "@/directive/ScrollClass";
import {
  defineProps,
  onBeforeMount,
  ref,
  watch,
  onMounted,
} from "vue";
import dataLife from "@/data/dataLiffe.Json";
import { useRoute, useRouter } from "vue-router";
import { Swiper, SwiperSlide } from 'swiper/vue';
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Navigation, Pagination } from 'swiper/modules';
const modules = [Navigation, Pagination];

const route = useRoute();
const router = useRouter();

const props = defineProps({
  id: {},
  name: String,
});

const thumbsSwiper = ref(null);
const data = ref({});
const dataDetail = ref({});
const refModal = ref(null);
const dataNext = ref(null);
const dataPrev = ref(null);
const showImg = ref(0)

onBeforeMount(() => {
  window.scrollTo(0, 0);
  data.value = dataLife;
  const indexData = Number(route.query.index);

  dataDetail.value = data.value.find((item) => item.id == props.id);
  if (indexData < data.value.length - 2) {
    dataNext.value = data.value[indexData + 1];
    dataNext.value.index = indexData + 1;
  }
  if (indexData > 0) {
    dataPrev.value = data.value[indexData - 1];
    dataPrev.value.index = indexData - 1;
  }
});

onMounted(() => {
  window.scrollTo(0, 0);
});

const showModal = (index) => {
  thumbsSwiper.value = null
  showImg.value = index

  refModal.value.handleShowModal();
};
// const setThumbsSwiper = (swiper) => {
//   thumbsSwiper.value = swiper;
// };

// const handelNavigate = (id, index) => {
//   const params = { id: id, name: props.name };
//   const query = { index: index, timestamp: Date.now() };
//   router.replace({ name: "Detail", params, query });
// };
watch(
  () => route.query,
  (newQuery, oldQuery) => {
    if (newQuery.timestamp !== oldQuery.timestamp) {
      router.go(0);
      window.scrollTo(0, 0);
    }
  }
);
</script>
